.App {
  position: relative;
  min-height: 100vh;
  background-color: var(--bg-primary);
}

.main-content {
  position: relative;
  z-index: 1;
  padding-top: 80px;
}

@media (max-width: 768px) {
  .main-content {
    padding-top: 70px;
  }
}

/* ===== Background video (full-cover) ===== */
#bg-video {
  position: fixed;
  inset: 0;               /* top:0; right:0; bottom:0; left:0; */
  width: 100%;
  height: 100%;
  object-fit: cover;      /* makes video behave like background-size: cover */
  z-index: -2;
  pointer-events: none;   /* so clicks go through to your UI */
  opacity: 1;             /* change if you want the raw video dimmer */
}

/* Overlay above the video for better contrast */
#video-overlay {
  position: fixed;
  inset: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.45); /* adjust opacity to taste */
  z-index: -1;
  pointer-events: none;
}

/* Ensure your app's content is above the overlay */
.App {
  position: relative;
  z-index: 1;
}

/* Optional: ensure nav and other interactive elements have higher stacking if needed */
nav, header, .main-content {
  position: relative;
  z-index: 2;
}
